.msg {
    box-shadow: var(--main-shadow);
  padding: 10px 20px;
  border-radius: 40px;
  width: 400px;
  font-size: 14px;
}

.my-msg {
  color: white;
  background-color: var(--main-clr);
}

.other-msg h4 {
  text-transform: capitalize;
  font-size: 17px;
}

.other-msg p {
  color: #555;
}

@media (max-width: 767px) {
  .msg {
    width: 200px;
    font-size: 10px;
  }

  .my-msg {
    transform: translateX(79px) rotate(180deg) !important ;
  }

  .other-msg h4 {
    font-size: 14px;
  }
}