.welcome-page {
  box-shadow: var(--main-shadow);
  padding: 50px 25px;
  border-radius: 30px;
  text-align: center;
  width: fit-content;
  top: 55%;
}

.welcome-page h3 {
  font-size: 13px;
  color: #555;
  margin: 10px 0 15px;
}

.welcome-page input {
  text-align: center;
}

.welcome-page button {
  display: block;
  width: 200px;
  padding: 10px;
  border-radius: 50px;
  border: none;
  background: var(--main-clr);
  color: white;
  font-weight: bold;
  margin-top: 10px;
  cursor: pointer;
}

.welcome-page button:hover {
  background-color: 	hsl(209, 81%, 55%)
}