input {
  padding: 10px 20px;
  border-radius: 100px;
  box-shadow: var(--main-shadow);
  border: none;
  color: #444;
  font-size: 12px;
}

input:focus {
  outline: none;
}

.big {
  width: 600px;
}

.small {
  width: 200px;
}
@media (max-width: 767px) {
  .big {
    width: 300px;
  }
}
form {
  width: fit-content;
  position: relative;
}

.send {
  width: 27px;
  height: 27px;
  border-radius: 50%;
  border: none;
  background-color: var(--main-clr);
  position: absolute;
  right: 5px;
  top: 50%;
  transform: translatey(-50%);
}

.send::before {
  content: '';
  position: absolute;
  width: 7px;
  height: 5px;
  background-color: white;
  top: 50%;
  left: 50%;
  transform: translate(-35%, -50%);
  width: 0;
  height: 0;
  border-top: 7px solid transparent;
  border-bottom: 7px solid transparent;
  border-left: 11px solid white;
  background: transparent;
}