.show-msgs {
  width: 600px;
  height: 500px;
  border-radius: 30px;
  margin-bottom: 10px;
  box-shadow: var(--main-shadow);
  padding: 10px;
  position: relative;
  overflow: scroll;
  transform: rotate(180deg);
}
@media (max-width: 767px) {
  .show-msgs {
    width: 300px;
    height: 450px;
  }
}

.show-msgs::-webkit-scrollbar {
  width: 0;
}
.msg:not(:last-child) {
  margin-bottom: 10px;
}

.other-msg {
  transform: rotate(180deg);

}

.my-msg {
  right: 10px;
  transform: translatex(179px) rotate(180deg);
}
