:root {
  --main-clr: #469CEC;  
  --main-shadow: 1px 1px 5px 0 hsl(0 0% 0% / .25);
}

body {
  margin: 0;
  font-family: Arial, Helvetica, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;

}

*,
*::before,
*::after {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

.app {
  min-height: 100vh;
}

.main-title {
  position: fixed;
  top: 30px;
}

.main-title::before {
  content: '';
  position: absolute;
  background-color: var(--main-clr);
  width: 110%;
  bottom: -7px;
  height: 3px;
  left: -5%;
}

@media (max-width: 767px) {
  .main-title {
    font-size: 24px;
  }
}

.centered-h {
  position: absolute;
  left: 50%;
  transform: translatex(-50%);
}

.centered {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.msgs-page {
  top: 100px;
}
